import React, { Component } from "react";

export default class pub extends Component {
  constructor() {
    super();
    this.state = {
      title: undefined,
      description: undefined,
      topic: "Artificial intelligence",
      author: "Adnan Abdullah",
      file: null,
    };
  }
  onsubmit=()=>{
      
  }
  ontitlechange = (e) => {
    this.setState({ title: e.target.value });
  };
  ondescriptionchange = (e) => {
    this.setState({ description: e.target.value });
  };
  ontopicChange = (e) => {
    this.setState({ topic: e.target.value });
  };
  onauthorChange = (e) => {
    this.setState({ author: e.target.value });
  };
  onfileupload = (e) => {
    this.setState({ file: e.target.files });
  };
  render() {
    return (
      <div>
        <p className="title">Add Blog</p>
        <form>
          <input
            onChange={this.ontitlechange}
            style={{ border: "1px solid #29303b", width: "600px" }}
            required
            className="F-classInput"
            type="text"
            placeholder="Title*"
          ></input>
          <select
            onChange={this.onauthorChange}
            required
            style={{ border: "1px solid #29303b", width: "620px" }}
            className="F-classInput"
          >
            <option className="F-classInput" value="Adnan Abdullah">
              Adnan Abdullah
            </option>
            <option className="F-classInput" value="MD Jamil Kashem Porosh">
              MD Jamil Kashem Porosh
            </option>
            <option className="F-classInput" value="MD Jamil Kashem Porosh">
              Mamun Khan
            </option>
          </select>
          <select
            onChange={this.ontopicChange}
            required
            style={{ border: "1px solid #29303b", width: "620px" }}
            className="F-classInput"
          >
            <option className="F-classInput" value="Business intelligence">
              Business intelligence
            </option>
            <option className="F-classInput" value="Artificial intelligence">
              Artificial intelligence
            </option>
            <option className="F-classInput" value="Web Development">
              Web Development
            </option>
            <option className="F-classInput" value="Graphics Design">
              Graphics Design
            </option>
          </select>
          <textarea
            onChange={this.ondescriptionchange}
            placeholder="Description*"
            required
            style={{
              border: "1px solid #29303b",
              width: "590px",
              height: "250px",
            }}
            className="textarea-pub"
          ></textarea>
          <input
            onChange={this.onfileupload}
            type="file"
            required
            className="F-classInput"
            style={{ border: "none", width: "180px" }}
          ></input>
          <button className="submit-btn-cntct" style={{ fontSize: "30px" }}>
            {"+"}
          </button>
        </form>
      </div>
    );
  }
}
